import React, { useState, useRef, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import Common from '../../components/main/Common';
import {
    TitleSec,
    Tit,
    SubTit,
    UploadSec,
    Lable,
    Input,
    PostUploadImg,
    DeleteBtn,
    Img,
    FileUpload,
    FileInput,
    UploadSubSec,
} from './postStyle';
import { PostUploadFooter } from './UploadFooter/uploadfooter';
import { URL as url } from '../../lib/apis/constant/path';
import ImgUploadBtn from '../../assets/icons/post/icon-image.png';
import { ValidContent, ValidLength } from './valiPostUpload';
import { useDispatch, useSelector } from 'react-redux';
import { CONTENT_UPDATE_OK, FAIL_OVER_IMAGE } from "../../lib/apis/constant/message";
import Modal from "../../components/modal/Modal";
import { setContent as setContents, setIsVisible, setUrl } from '../../store/slices/modalSlice';

export default function PostUpload() {
    const { id } = useParams();
    const token = localStorage.getItem('token');
    const [content, setContent] = useState(''); //   
    const [showImg, setShowImg] = useState([]);
    const [postImg, setPostImg] = useState([]);
    const [uploadBtn, setUploadBtn] = useState(true);
    const fileInput = useRef(null);
    const navigate = useNavigate();
    const dispatch = useDispatch();

    //     state
    const { display : { isVisible }} = useSelector(state => state.modal);

    const data = {
        post: {
            content: '',
            image: '',
        },
    };

    // 1.   
    async function UploadImg(file) {
        const formData = new FormData();
        formData.append('image', file);

        const response = await axios.post(`${url}/image/uploadfile`, formData);
        const imgName = `${url}/` + response.data.filename;

        return imgName;
    }

    // 2.   
    async function ImgView(e) {
        //      !
        const files = e.target.files;
        let fileUrls = [...postImg];
        let fileImgs = [...showImg];
        //   10MB 
        const maxSize = 10 * 1024 * 1024;
        let TotalImgSize = 0;

        for (let i = 0; i < files.length; i++) {
            TotalImgSize += files[i].size;

            if (TotalImgSize > maxSize) {
                alert('    10MB.');
            } else {
                const createImgUrl = URL.createObjectURL(files[i]);
                fileImgs.push(await UploadImg(files[i]));
                fileUrls.push(createImgUrl);
            }
        }
        console.log(fileImgs);

        //  3 
        if (ValidLength(fileUrls)) {
            dispatch(setContents({
                state : false,
                message: FAIL_OVER_IMAGE
            })
            )
            dispatch(setIsVisible({isVisible : true}));

            fileUrls = fileUrls.slice(0, 3);
            fileImgs = fileImgs.slice(0, 3);
        }

        setShowImg(fileImgs);
        setPostImg(fileUrls);
    }

    // 3.   
    const DeleteImg = (id) => {
        setShowImg(
            showImg.filter((_, index) => {
                return index !== id;
            })
        );

        setPostImg(
            postImg.filter((_, index) => {
                return index !== id;
            })
        );
    };

    // 4.   
    async function UploadPost() {
        const imgList = [...showImg];
        console.log(imgList);

        // for (let i = 0; i < postImg.length; i++) {
        //     console(postImg[i]);
        //     imgList.push(await UploadImg(postImg[i]));
        // }

        const snsImgList = await Promise.all(imgList);

        data.post.image = snsImgList.join(',');
        data.post.content = content;

        try {
            const res = await axios
                .put(`${url}/post/${id}`, data, {
                    headers: {
                        Authorization: `Bearer ${token}`,
                        'Content-type': 'application/json',
                    },
                })
                
                if (res.status === 200){
                    dispatch(setContents({
                        state : true,
                        message: CONTENT_UPDATE_OK
                    })
                    )
                    dispatch(setIsVisible({isVisible : true}));
                    dispatch(setUrl({ path : '/home'}))
                }
            } catch (error) {
                const errorMessage = error.response.data.message
                dispatch(setContents({
                    state : false,
                    message: errorMessage
                })
                )
                dispatch(setIsVisible({isVisible : true}));
            }
        }

    async function ModifyPost() {
        const {
            data: {
                // post      
                post,
            },
        } = await axios.get(`${url}/post/${id}`, {
            headers: {
                Authorization: `Bearer ${token}`,
                'Content-type': 'application/json',
            },
        });
        console.log(post);

        //      join    split 
        setPostImg(post.image.split(','));
        setShowImg(post.image.split(','));
        setContent(post.content);
    }

    //        .   
    useEffect(() => {
        ModifyPost();
    }, []);

    //        
    useEffect(() => {
        setUploadBtn( ValidContent(content, postImg))
    }, [content, postImg]);

    const page = (
        <>
            <title>   </title>
            <meta name="description" content="  " />

            {/*    section */}
            <UploadSec>
                <TitleSec>
                    <Tit></Tit>
                    <SubTit>     .</SubTit>
                </TitleSec>

                {/*   */}
                <Lable htmlFor="post" />
                <Input
                    name="post"
                    id="post"
                    placeholder=" ."
                    value={content}
                    // input   
                    onChange={(e) => {
                        setContent(e.target.value);
                    }}
                />

                {/*    */}
                <PostUploadImg>
                        <ul>
                            {postImg.length !== 0
                                ? postImg.map((image, id) => {
                                        return (
                                            //     
                                            image && (
                                                <li key={id}>
                                                    <Img key={id} src={image} />
                                                    <DeleteBtn
                                                        onClick={() => {
                                                            return DeleteImg(id);
                                                        }}
                                                    />
                                                </li>
                                            )
                                        );
                                    })
                                    : postImg.map((image, id) => {
                                        return (
                                            //      
                                            <li key={id}>
                                                <Img key={id} src={image} />
                                                <DeleteBtn
                                                    onClick={() => {
                                                        return DeleteImg(id);
                                                    }}
                                                />
                                            </li>
                                        );
                                    })}
                        </ul>
                    </PostUploadImg>

                <UploadSubSec>
                    {/*    */}
                    <FileUpload htmlFor="input-file">
                        <img src={ImgUploadBtn} alt="" />
                        <FileInput
                            id="input-file"
                            name="PostImg"
                            type="file"
                            accept=".png, .jpg, .jpeg"
                            onChange={ImgView}
                            ref={fileInput}
                        />
                    </FileUpload>

                    <PostUploadFooter
                    UploadPost={UploadPost}
                />
                </UploadSubSec>
            </UploadSec>
            {
                isVisible && <Modal />
            }
        </>
    );

    return (
        <>
            {/* {console.log(showImg)} */}
            <Common page={page} />
        </>
    );
}
