import { useCallback, useState } from 'react';
import { debounce } from 'lodash';
import UserLIst from './UserList';
import { api } from '../../lib/apis/axiosConfig';
import Common from '../main/Common';
import { keyframes, styled } from 'styled-components';
import logo from '../../assets/logo/LOGO.svg';
import searchIconWhite from '../../assets/icons/common/search-main-white.svg';
import searchBack from '../../assets/images/common/search-image.png';

export default function Search() {
    // console.log("Search");
    const [search, setSearch] = useState('');
    //  
    const [userList, setUserList] = useState([]);
    //    
    const [showUser, setShowUser] = useState([]);
    // 
    const [page, setPage] = useState(0);
    const [isLoading, setIsLoading] = useState(false);

    const sendQuery = async (q) => {
        if (!isLoading) {
            setIsLoading(true);
        }
        delayLoading();
        if (q.length === 0) return;
        const { data } = await api.get(`/user/searchuser/?keyword=${q}`);
        // userList  
        setUserList(data);
        // 10  
        setShowUser(data.slice(page * 10, page * 10 + 10));
        //        
        setPage(page + 1);
    };

    const delayedSearch = useCallback(
        debounce((q) => sendQuery(q), 400),
        []
    );

    const delayLoading = useCallback(debounce((q) => setIsLoading(false), 500));

    const handleTyping = (e) => {
        setSearch(e.target.value);
        if (e.target.value === '') {
            setUserList('');
            setShowUser('');
            return;
        }
        delayedSearch(e.target.value);
    };

    const addShowUser = () => {
        const addUserList = userList.slice(page * 10, page * 10 + 10);
        setShowUser([...showUser, ...addUserList]);
        setPage(page + 1);
    };

    const pageTitle = ' ';
    const pageDesc = '   .';

    return (
        <Common
            page={
                <SearchWrapper>
                    {isLoading && (
                        <Loading>
                            <img src={logo} alt="logo" />
                        </Loading>
                    )}
                    <Input
                        id="search_input"
                        type="text"
                        placeholder=" "
                        onChange={handleTyping}
                        value={search}
                    />
                    <List>
                        {search && showUser && (
                            <UserLIst
                                showUser={showUser}
                                searchQuery={search}
                            />
                        )}
                        {
                            //         
                            search && showUser.length < userList.length ? (
                                <AddButton onClick={addShowUser}>
                                    
                                </AddButton>
                            ) : null
                        }
                    </List>
                    <SearchIcon htmlFor="search_input">
                        <img src={searchIconWhite} alt=" " />
                    </SearchIcon>
                </SearchWrapper>
            }
            title={pageTitle}
            desc={pageDesc}
        ></Common>
    );
}

const SearchWrapper = styled.article`
    position: relative;
    width: 350px;
    height: calc(100vh - 350px);
    margin: 0px auto 20px;
    padding: 190px 0 20px;
    overflow-y: hidden;
    background: url(${searchBack}) no-repeat;
    background-size: 130%;
    background-position: 50% 120%;
    // background-color: red;
`;

const Input = styled.input`
    position: absolute;
    top: 0;
    left: 50%;
    width: 50px;
    height: 50px;
    margin: 130px auto;
    border: 3px solid var(--color-main);
    border-radius: 25px;
    background-color: white;
    box-sizing: border-box;
    color: #333;
    transform: translateX(-50%);

    &:focus,
    &:hover,
    &:not(:placeholder-shown) {
        width: 300px;
    }

    &:focus ~ label,
    &:hover ~ label,
    &:not(:placeholder-shown) ~ label {
        transform: translateX(100px);
    }
`;

const SearchIcon = styled.label`
    position: absolute;
    display: block;
    top: 0;
    left: 50%;
    width: 50px;
    height: 50px;
    margin: 130px auto;
    border-radius: 50%;
    box-sizing: border-box;
    background-color: var(--color-main);
    transform: translateX(-50%);
    transition: transform 0.3s;
    & > img {
        width: 50px;
        height: 50px;
        object-fit: none;
        object-position: 50% 50%;
    }
`;

const AddButton = styled.button`
    all: unset;
    display: block;
    width: 100px;
    height: 20px;
    margin: 0 auto 20px;
    border-radius: var(--radius-m);
    font-size: var(--fsize-m);
    text-align: center;
    cursor: pointer;
`;

const shakeAnimation = keyframes`
    0%{transform : translate(-50%, -50%) rotate(-20deg);}

    25%{transform : translate(-50%, -50%) rotate(20deg);}

    50%{transform : translate(-50%, -50%) rotate(-20deg);}

    75%{transform : translate(-50%, -50%) rotate(20deg);}
`;

const Loading = styled.div`
    position: absolute;
    top: 50%;
    left: 50%;
    width: 70px;
    height: 70px;
    animation: ${shakeAnimation} 1s ease-in-out infinite;
    & > img {
        width: 70px;
    }
`;

const List = styled.div`
    height: 530px;
    margin-top: 10px;
    border-top: 1px solid var(--color-disabled);
    border-bottom: 1px solid var(--color-disabled);
    overflow-y: auto;
`;
