import React, { useState, useRef, useEffect } from 'react';
// import { useSelector} from 'react-redux';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import Common from '../../components/main/Common';
import {
    TitleSec,
    Tit,
    SubTit,
    UploadSec,
    Lable,
    Input,
    PostUploadImg,
    DeleteBtn,
    Img,
    FileUpload,
    FileInput,
    UploadSubSec,
} from './postStyle';
import { PostUploadFooter } from './UploadFooter/uploadfooter';
import { URL } from '../../lib/apis/constant/path';
import ImgUploadBtn from '../../assets/icons/post/icon-image.png';
import { ValidContent, ValidLength } from './valiPostUpload';
import { useDispatch, useSelector } from 'react-redux';
import {
    setContent as setContents,
    setIsVisible,
    setUrl,
} from '../../store/slices/modalSlice';
import {
    FAIL_OVER_IMAGE,
    POST_UPLOAD_OK,
} from '../../lib/apis/constant/message';
import Modal from '../../components/modal/Modal';
export default function PostUpload() {
    const token = localStorage.getItem('token');
    const [content, setContent] = useState(''); //   
    const [showImg, setShowImg] = useState([]);
    const [postImg, setPostImg] = useState([]);
    const [uploadBtn, setUploadBtn] = useState(true);
    const fileInput = useRef(null);
    const navigate = useNavigate();
    const dispatch = useDispatch();
    // const { myInfo } = useSelector((state) => state.user);
    // console.log(myInfo);

    //    state
    const {
        display: { isVisible },
    } = useSelector((state) => state.modal);

    const data = {
        post: {
            content: '',
            image: '',
        },
    };

    // 1.   
    //      filename  
    async function UploadImg(file) {
        const formData = new FormData();
        formData.append('image', file);

        const response = await axios.post(`${URL}/image/uploadfile`, formData);
        const imgName = `${URL}/` + response.data.filename;
        console.log(imgName);
        return imgName;
    }

    // 2.   
    function ImgView(e) {
        //      !
        const files = e.target.files;
        console.log(files);
        let fileUrls = [...showImg];
        let fileImgs = [...postImg];

        //   10MB 
        const maxSize = 10 * 1024 * 1024;
        let TotalImgSize = 0;

        for (let i = 0; i < files.length; i++) {
            TotalImgSize += files[i].size;

            if (TotalImgSize > maxSize) {
                alert('    10MB.');
            } else {
                const createImgUrl = window.URL.createObjectURL(files[i]);
                fileUrls.push(createImgUrl);
                fileImgs.push(files[i]);
            }
        }

        //  3 
        if (ValidLength(fileUrls)) {
            dispatch(
                setContents({
                    state: false,
                    message: FAIL_OVER_IMAGE,
                })
            );
            dispatch(setIsVisible({ isVisible: true }));

            fileUrls = fileUrls.slice(0, 3);
            fileImgs = fileImgs.slice(0, 3);
        }

        setShowImg(fileUrls);
        setPostImg(fileImgs);
    }

    // 3.   
    const DeleteImg = (id) => {
        setShowImg(
            showImg.filter((_, index) => {
                return index !== id;
            })
        );

        setPostImg(
            postImg.filter((_, index) => {
                return index !== id;
            })
        );
    };

    // 4.   
    async function UploadPost() {
        // UploadImg  filename  url  
        const imgList = [];

        for (let i = 0; i < postImg.length; i++) {
            // filename  url  imgList push
            imgList.push(UploadImg(postImg[i]));
        }

        const snsImgList = await Promise.all(imgList);

        data.post.image = snsImgList.join(',');
        data.post.content = content;

        //    
        try {
            const res = await axios.post(`${URL}/post`, data, {
                headers: {
                    Authorization: `Bearer ${token}`,
                    'Content-type': 'application/json',
                },
            });

            if (res.status === 200) {
                dispatch(
                    setContents({
                        state: true,
                        message: POST_UPLOAD_OK,
                    })
                );
                dispatch(setIsVisible({ isVisible: true }));
                dispatch(setUrl({ path: '/home' }));
            }
        } catch (error) {
            const errorMessage = error.response.data.message;
            dispatch(
                setContents({
                    state: false,
                    message: errorMessage,
                })
            );
            dispatch(setIsVisible({ isVisible: true }));
        }
    }

    //        
    useEffect(() => {
        setUploadBtn(ValidContent(content, postImg));
    }, [content, postImg]);

    const page = (
        <>
            <title>   </title>
            <meta name="description" content="  " />

            {/*    section */}
            <UploadSec>
                <TitleSec>
                    <Tit></Tit>
                    <SubTit>     .</SubTit>
                </TitleSec>

                {/*   */}
                <Lable htmlFor="post" />
                <Input
                    name="post"
                    id="post"
                    placeholder=" ."
                    value={content}
                    // input   
                    onChange={(e) => {
                        setContent(e.target.value);
                    }}
                />

                {/*    */}
                <PostUploadImg>
                    <ul>
                        {showImg.map((image, id) => {
                            return (
                                <li key={id}>
                                    <Img key={id} src={image} />
                                    <DeleteBtn
                                        onClick={() => {
                                            return DeleteImg(id);
                                        }}
                                    />
                                </li>
                            );
                        })}
                    </ul>
                </PostUploadImg>

                <UploadSubSec>
                    {/*    /  */}
                    <FileUpload htmlFor="input-file">
                        <img src={ImgUploadBtn} alt="" />
                    </FileUpload>
                    <FileInput
                        id="input-file"
                        name="PostImg"
                        type="file"
                        accept=".png, .jpg, .jpeg"
                        onChange={ImgView}
                        ref={fileInput}
                    />

                    {/*    */}
                    <PostUploadFooter UploadPost={UploadPost} />
                </UploadSubSec>
            </UploadSec>
            {isVisible && <Modal />}
        </>
    );

    return (
        <>
            <Common page={page} />
        </>
    );
}
